h1 { font-family: 'Merriweather', serif; }
body { font-family: 'Crimson Pro', serif; }

/* EDITORIAL */
.editorialFeatured {
  height: 500px;
  background-image: url('../assets/student-health.jpg');
  background-size: cover;
  display: flex;
  align-self: center;
  justify-content: flex-start;
}

.editorial-feature-content{
  text-align: center;
  align-self: center;
  margin-left: 5%;
  width: 50%;
  color: black;
  /* font-family: 'Montserrat', sans-serif; */
  font-size: 20px;
  padding: 0;
}

.hoverEffect {
  transition: all .2s ease-in-out;
}

.hoverEffect:hover {
  transform: scale(1.05); 
}


/* NEW S*/
.newsFeatured {
  text-align: center;   
  font-family: 'Montserrat', sans-serif;
  font-size: 25px;
}

.newsGrid {
  display: grid;
  grid-auto-columns: 1fr 1fr;
  grid-auto-rows: repeat(10, 1fr);
  font-size: 16px;
  grid-gap: 3em;
}

.newsGrid > * {
  border: 1px solid black;
  font-family: 'Crimson Pro', serif; 
}
.newsGrid > div { transition: all .2s ease-in-out; }
.newsGrid > div:hover { transform: scale(1.05); }

.image {
  max-width:100%;
  max-height:100%;
  z-index: -10;
  position: absolute;
}

.overflow {
  overflow: hidden;
  position: relative;
}

.newsGrid h2 {
  padding: 0px 15px 0px 15px;
}

.newsGrid h3 {
  padding: 0px 15px 0px 15px;
  font-style: italic;
}

.news1 {
  background-image: url("https://static01.nyt.com/images/2019/07/31/us/politics/31xp-craft/merlin_156678108_8c0133b2-d12e-401d-90e4-7fc336c588db-mediumThreeByTwo210.jpg");
  background-size: cover;
  grid-column: 1 / 2;
  grid-row: 1 / 2;
  height: 250px;
  width: auto;
  display: flex;
}

/* .news1:hover { transform: scale(1.05); } */

